@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;500;600;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Outfit", "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
}
h1,
h4,
p,
a {
  color: #fff;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}
code {
  font-family: source-code-pro, Manlon, Monaco, Consolas, "Courier New",
    monospace;
}
.btn {
  padding: 12px 32px;
  font-size: 1.2rem;
  text-transform: uppercase;
  background: transparent;
  color: white;
  border: 1px solid white;
  cursor: pointer;
}
.btn-light {
  background: rgba(255, 255, 255, 0.2);
}
.btn:hover {
  background: rgba(255, 255, 255, 0.2);
}
